"use client";

import React, { useState, useEffect } from 'react';
import { use } from 'react';

export default function Page({params}) {
  const { id } = use(params);
  const [items, setItems] = useState([]);
  
  // State สำหรับแต่ละ field
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [firstname, setFirstname] = useState('');
  const [fullname, setFullname] = useState('');
  const [lastname, setLastname] = useState('');
  const [address, setAddress] = useState('');
  const [sex, setSex] = useState('');
  const [birthday, setBirthday] = useState('');
  const [acceptTerms, setAcceptTerms] = useState(false);
  const [errors, setErrors] = useState({});
  const [isLoading, setIsLoading] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const [passwordStrength, setPasswordStrength] = useState({
    score: 0,
    feedback: '',
    color: '#dc3545'
  });

  // Mock alert function for demonstration
  const showAlert = (type, title, message) => {
    alert(`${type.toUpperCase()}: ${title}\n${message}`);
  };

  // ดึงข้อมูลผู้ใช้เมื่อ component mount
  useEffect(() => {
    const fetchUserData = async () => {
      try {
        // Mock data for demonstration
        const userData = {
          username: 'john_doe',
          firstname: 'นาย',
          fullname: 'จอห์น',
          lastname: 'โด',
          address: '123 ถนนสุขุมวิท กรุงเทพฯ',
          sex: 'ชาย',
          birthday: '1990-05-15'
        };
        
        setItems([userData]);
        setUsername(userData.username || '');
        setFirstname(userData.firstname || '');
        setFullname(userData.fullname || '');
        setLastname(userData.lastname || '');
        setAddress(userData.address || '');
        setSex(userData.sex || '');
        setBirthday(userData.birthday ? userData.birthday.split('T')[0] : '');
        setAcceptTerms(true);
      } catch (error) {
        console.error('Error fetching user data:', error);
      }
    };

    if (id) {
      fetchUserData();
    }
  }, [id]);

  const validateForm = () => {
    const newErrors = {};
    
    if (!username) newErrors.username = 'กรุณากรอกชื่อผู้ใช้';
    else if (username.length < 3) newErrors.username = 'ชื่อผู้ใช้ต้องมีอย่างน้อย 3 ตัวอักษร';
    
    if (password && password.length < 6) newErrors.password = 'รหัสผ่านต้องมีอย่างน้อย 6 ตัวอักษร';
    
    if (!firstname) newErrors.firstname = 'กรุณาเลือกคำนำหน้าชื่อ';
    if (!fullname) newErrors.fullname = 'กรุณากรอกชื่อ';
    if (!lastname) newErrors.lastname = 'กรุณากรอกนามสกุล';
    if (!address) newErrors.address = 'กรุณากรอกที่อยู่';
    if (!sex) newErrors.sex = 'กรุณาเลือกเพศ';
    if (!birthday) newErrors.birthday = 'กรุณาเลือกวันเกิด';
    if (!acceptTerms) newErrors.acceptTerms = 'กรุณายอมรับเงื่อนไข';

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const validatePasswordStrength = (password) => {
    if (!password) {
      setPasswordStrength({ score: 0, feedback: '', color: '#dc3545' });
      return;
    }

    let score = 0;
    let feedback = [];
    
    if (password.length >= 8) score += 1;
    else feedback.push('อย่างน้อย 8 ตัวอักษร');
    
    if (/[a-z]/.test(password)) score += 1;
    else feedback.push('ตัวพิมพ์เล็ก');
    
    if (/[A-Z]/.test(password)) score += 1;
    else feedback.push('ตัวพิมพ์ใหญ่');
    
    if (/[0-9]/.test(password)) score += 1;
    else feedback.push('ตัวเลข');
    
    if (/[^A-Za-z0-9]/.test(password)) score += 1;
    else feedback.push('อักขระพิเศษ');

    let strengthText = '';
    let color = '#dc3545';
    
    if (score <= 2) {
      strengthText = 'อ่อนแอ';
      color = '#dc3545';
    } else if (score === 3) {
      strengthText = 'ปานกลาง';
      color = '#ffc107';
    } else if (score === 4) {
      strengthText = 'ดี';
      color = '#17a2b8';
    } else {
      strengthText = 'แข็งแกร่งมาก';
      color = '#28a745';
    }

    setPasswordStrength({
      score: score,
      feedback: feedback.length > 0 ? `ขาด: ${feedback.join(', ')}` : 'รหัสผ่านแข็งแกร่ง!',
      color: color,
      strengthText: strengthText
    });
  };

  const handleUpdateSubmit = async (e) => {
    e.preventDefault();
    
    const isValid = validateForm();
    if (!isValid) {
      showAlert('warning', 'ข้อมูลไม่ครบถ้วน!', 'กรุณากรอกข้อมูลให้ครบถ้วนและถูกต้อง');
      return;
    }

    setIsLoading(true);

    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      showAlert('success', 'แก้ไขข้อมูลสำเร็จ!', 
        password ? 'ข้อมูลและรหัสผ่านของคุณถูกแก้ไขเรียบร้อยแล้ว' : 'ข้อมูลของคุณถูกแก้ไขเรียบร้อยแล้ว'
      );

      setPassword('');
      
    } catch (error) {
      showAlert('error', 'เกิดข้อผิดพลาด!', 'ไม่สามารถแก้ไขข้อมูลได้ กรุณาลองใหม่อีกครั้ง');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <>
      <style jsx>{`
        @import url('https://fonts.googleapis.com/css2?family=Noto+Sans+Thai:wght@300;400;500;600;700&display=swap');
        
        .sakura-container {
          background: linear-gradient(135deg, #ffeef8 0%, #fff0f5 25%, #ffe4e1 50%, #fdf2f8 75%, #fef7ff 100%);
          min-height: 100vh;
          position: relative;
          overflow: hidden;
          font-family: 'Noto Sans Thai', sans-serif;
        }
        
        .sakura-petals {
          position: absolute;
          width: 100%;
          height: 100%;
          overflow: hidden;
          z-index: 1;
        }
        
        .petal {
          position: absolute;
          width: 20px;
          height: 20px;
          background: linear-gradient(45deg, #ffb3d9, #ff99cc);
          border-radius: 50% 10px 50% 10px;
          animation: fall linear infinite;
          opacity: 0.7;
        }
        
        .petal:nth-child(1) { left: 10%; animation-duration: 8s; animation-delay: 0s; }
        .petal:nth-child(2) { left: 20%; animation-duration: 12s; animation-delay: 2s; }
        .petal:nth-child(3) { left: 30%; animation-duration: 10s; animation-delay: 1s; }
        .petal:nth-child(4) { left: 40%; animation-duration: 14s; animation-delay: 3s; }
        .petal:nth-child(5) { left: 50%; animation-duration: 9s; animation-delay: 0.5s; }
        .petal:nth-child(6) { left: 60%; animation-duration: 11s; animation-delay: 4s; }
        .petal:nth-child(7) { left: 70%; animation-duration: 13s; animation-delay: 1.5s; }
        .petal:nth-child(8) { left: 80%; animation-duration: 15s; animation-delay: 2.5s; }
        .petal:nth-child(9) { left: 90%; animation-duration: 7s; animation-delay: 3.5s; }
        
        @keyframes fall {
          0% {
            transform: translateY(-100px) rotate(0deg);
            opacity: 0.7;
          }
          50% {
            opacity: 1;
          }
          100% {
            transform: translateY(100vh) rotate(360deg);
            opacity: 0;
          }
        }
        
        .form-card {
          background: rgba(255, 255, 255, 0.95);
          backdrop-filter: blur(10px);
          border: 2px solid rgba(255, 182, 193, 0.3);
          box-shadow: 0 20px 40px rgba(255, 182, 193, 0.2);
          border-radius: 25px;
          position: relative;
          z-index: 10;
          font-family: 'Noto Sans Thai', sans-serif;
          max-width: 700px;
          margin: 20px auto;
        }
        
        .form-card::before {
          content: '';
          position: absolute;
          top: -2px;
          left: -2px;
          right: -2px;
          bottom: -2px;
          background: linear-gradient(45deg, #ffb3d9, #ff99cc, #ffb3d9);
          border-radius: 25px;
          z-index: -1;
        }
        
        .title-gradient {
          background: linear-gradient(135deg, #d63384, #6f42c1, #d63384);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
          background-clip: text;
          font-weight: 700;
          text-shadow: 0 4px 8px rgba(214, 51, 132, 0.2);
        }
        
        .form-input {
          background: rgba(255, 240, 245, 0.8);
          border: 2px solid rgba(255, 182, 193, 0.3);
          border-radius: 15px;
          padding: 12px 18px;
          font-family: 'Noto Sans Thai', sans-serif;
          transition: all 0.3s ease;
          box-shadow: 0 4px 8px rgba(255, 182, 193, 0.1);
          width: 100%;
          font-size: 16px;
        }
        
        .form-input:focus {
          outline: none;
          border-color: #d63384;
          background: rgba(255, 240, 245, 1);
          box-shadow: 0 0 20px rgba(214, 51, 132, 0.2);
          transform: translateY(-2px);
        }
        
        .form-input::placeholder {
          color: #d63384;
          font-weight: 400;
        }
        
        .form-input.is-invalid {
          border-color: #ff6b6b;
          box-shadow: 0 0 10px rgba(255, 107, 107, 0.3);
        }
        
        .password-input-container {
          position: relative;
        }
        
        .password-toggle-btn {
          position: absolute;
          right: 15px;
          top: 50%;
          transform: translateY(-50%);
          background: transparent;
          border: none;
          color: #d63384;
          cursor: pointer;
          padding: 5px;
          transition: color 0.3s ease;
          font-size: 18px;
        }
        
        .password-toggle-btn:hover {
          color: #6f42c1;
        }
        
        .password-strength {
          margin-top: 8px;
        }
        
        .strength-bar {
          height: 6px;
          border-radius: 3px;
          background: rgba(255, 182, 193, 0.3);
          overflow: hidden;
          margin-top: 4px;
        }
        
        .strength-fill {
          height: 100%;
          transition: all 0.3s ease;
          border-radius: 3px;
        }
        
        .submit-btn {
          background: linear-gradient(135deg, #d63384, #6f42c1);
          border: none;
          border-radius: 15px;
          padding: 15px 30px;
          color: white;
          font-weight: 600;
          font-family: 'Noto Sans Thai', sans-serif;
          font-size: 16px;
          transition: all 0.3s ease;
          box-shadow: 0 8px 16px rgba(214, 51, 132, 0.3);
          position: relative;
          overflow: hidden;
          width: 100%;
          cursor: pointer;
        }
        
        .submit-btn::before {
          content: '';
          position: absolute;
          top: 0;
          left: -100%;
          width: 100%;
          height: 100%;
          background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
          transition: left 0.5s;
        }
        
        .submit-btn:hover::before {
          left: 100%;
        }
        
        .submit-btn:hover {
          transform: translateY(-3px);
          box-shadow: 0 12px 24px rgba(214, 51, 132, 0.4);
        }
        
        .submit-btn:active {
          transform: translateY(-1px);
        }
        
        .submit-btn:disabled {
          background: linear-gradient(135deg, #ccc, #999);
          cursor: not-allowed;
          transform: none;
          box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
        }
        
        .floating-sakura {
          position: absolute;
          font-size: 24px;
          animation: float 6s ease-in-out infinite;
        }
        
        .floating-sakura:nth-child(1) {
          top: 10%;
          left: 5%;
          animation-delay: 0s;
        }
        
        .floating-sakura:nth-child(2) {
          top: 20%;
          right: 5%;
          animation-delay: 2s;
        }
        
        .floating-sakura:nth-child(3) {
          bottom: 10%;
          left: 8%;
          animation-delay: 4s;
        }
        
        .floating-sakura:nth-child(4) {
          bottom: 20%;
          right: 8%;
          animation-delay: 1s;
        }
        
        @keyframes float {
          0%, 100% { transform: translateY(0px) rotate(0deg); }
          33% { transform: translateY(-20px) rotate(5deg); }
          66% { transform: translateY(-10px) rotate(-5deg); }
        }
        
        .row {
          display: flex;
          gap: 15px;
          margin-bottom: 20px;
        }
        
        .col-md-4, .col-md-6 {
          flex: 1;
        }
        
        .col-md-4 {
          flex: 0 0 33.333333%;
        }
        
        .form-check {
          display: flex;
          align-items: center;
          gap: 10px;
          margin-bottom: 10px;
        }
        
        .form-check input[type="radio"], .form-check input[type="checkbox"] {
          width: 20px;
          height: 20px;
          accent-color: #d63384;
        }
        
        .form-check label {
          color: #6f42c1;
          font-weight: 500;
          cursor: pointer;
          display: flex;
          align-items: center;
          gap: 5px;
        }
        
        .invalid-feedback {
          color: #ff6b6b;
          font-size: 14px;
          margin-top: 5px;
          display: block;
        }
        
        .loading-spinner {
          display: inline-block;
          width: 20px;
          height: 20px;
          border: 2px solid #ffffff;
          border-radius: 50%;
          border-top-color: transparent;
          animation: spin 1s linear infinite;
          margin-right: 8px;
        }
        
        @keyframes spin {
          to { transform: rotate(360deg); }
        }
        
        .text-muted {
          color: #6f42c1;
          font-size: 14px;
        }
        
        .link-style {
          color: #d63384;
          text-decoration: none;
          font-weight: 600;
        }
        
        .link-style:hover {
          text-decoration: underline;
        }
        
        @media (max-width: 768px) {
          .row {
            flex-direction: column;
            gap: 0;
          }
          
          .col-md-4, .col-md-6 {
            flex: none;
            margin-bottom: 20px;
          }
          
          .form-card {
            margin: 10px;
            padding: 20px;
          }
        }
        
        .container {
          padding: 20px;
          min-height: 100vh;
          display: flex;
          align-items: center;
          justify-content: center;
        }
      `}</style>

      <div className="sakura-container">
        <div className="sakura-petals">
          <div className="petal"></div>
          <div className="petal"></div>
          <div className="petal"></div>
          <div className="petal"></div>
          <div className="petal"></div>
          <div className="petal"></div>
          <div className="petal"></div>
          <div className="petal"></div>
          <div className="petal"></div>
        </div>
        
        <div className="floating-sakura">🌸</div>
        <div className="floating-sakura">🌺</div>
        <div className="floating-sakura">🌸</div>
        <div className="floating-sakura">🌺</div>
        
        <div className="container">
          <div className="form-card" style={{padding: '40px'}}>
            <div style={{textAlign: 'center', marginBottom: '30px'}}>
              <h1 className="title-gradient" style={{fontSize: '2.5rem', marginBottom: '15px'}}>
                🌸 แก้ไขข้อมูลสมาชิก 🌸
              </h1>
              <p style={{color: '#6f42c1', fontWeight: '500', fontSize: '1.1rem'}}>
                ปรับปรุงข้อมูลของคุณให้เป็นปัจจุบัน
              </p>
            </div>

            {items.length === 0 ? (
              <div style={{textAlign: 'center', padding: '40px'}}>
                <div className="loading-spinner"></div>
                <p className="text-muted">กำลังโหลดข้อมูล...</p>
              </div>
            ) : (
              <form onSubmit={handleUpdateSubmit}>
                {/* Username */}
                <div style={{marginBottom: '20px'}}>
                  <input
                    type="text"
                    value={username}
                    onChange={(e) => setUsername(e.target.value)}
                    className={`form-input ${errors.username ? 'is-invalid' : ''}`}
                    placeholder="👤 ชื่อผู้ใช้"
                    disabled={isLoading}
                    required
                  />
                  {errors.username && <div className="invalid-feedback">{errors.username}</div>}
                </div>

                {/* Password */}
                <div style={{marginBottom: '20px'}}>
                  <div className="password-input-container">
                    <input
                      type={showPassword ? 'text' : 'password'}
                      value={password}
                      onChange={(e) => {
                        setPassword(e.target.value);
                        validatePasswordStrength(e.target.value);
                      }}
                      className={`form-input ${errors.password ? 'is-invalid' : ''}`}
                      placeholder="🔐 รหัสผ่านใหม่ (เว้นว่างไว้หากไม่ต้องการเปลี่ยน)"
                      disabled={isLoading}
                      style={{paddingRight: '50px'}}
                    />
                    <button
                      type="button"
                      className="password-toggle-btn"
                      onClick={() => setShowPassword(!showPassword)}
                      disabled={isLoading}
                    >
                      {showPassword ? '👁️' : '🙈'}
                    </button>
                  </div>
                  {errors.password && <div className="invalid-feedback">{errors.password}</div>}
                  
                  {password && (
                    <div className="password-strength">
                      <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '5px'}}>
                        <small className="text-muted">ความแข็งแกร่งของรหัสผ่าน:</small>
                        <small style={{color: passwordStrength.color, fontWeight: 'bold'}}>
                          {passwordStrength.strengthText}
                        </small>
                      </div>
                      <div className="strength-bar">
                        <div 
                          className="strength-fill"
                          style={{ 
                            width: `${(passwordStrength.score / 5) * 100}%`,
                            backgroundColor: passwordStrength.color
                          }}
                        />
                      </div>
                      <small className="text-muted" style={{display: 'block', marginTop: '5px'}}>
                        {passwordStrength.feedback}
                      </small>
                    </div>
                  )}
                </div>

                {/* Name fields */}
                <div className="row">
                  <div className="col-md-4">
                    <select
                      value={firstname}
                      onChange={(e) => setFirstname(e.target.value)}
                      className={`form-input ${errors.firstname ? 'is-invalid' : ''}`}
                      disabled={isLoading}
                      required
                    >
                      <option value="">🌸 เลือกคำนำหน้าชื่อ</option>
                      <option value="นาย">นาย</option>
                      <option value="นาง">นาง</option>
                      <option value="นางสาว">นางสาว</option>
                    </select>
                    {errors.firstname && <div className="invalid-feedback">{errors.firstname}</div>}
                  </div>

                  <div className="col-md-4">
                    <input
                      type="text"
                      placeholder="🌺 ชื่อ"
                      value={fullname}
                      onChange={(e) => setFullname(e.target.value)}
                      className={`form-input ${errors.fullname ? 'is-invalid' : ''}`}
                      disabled={isLoading}
                      required
                    />
                    {errors.fullname && <div className="invalid-feedback">{errors.fullname}</div>}
                  </div>

                  <div className="col-md-4">
                    <input
                      type="text"
                      placeholder="🌸 นามสกุล"
                      value={lastname}
                      onChange={(e) => setLastname(e.target.value)}
                      className={`form-input ${errors.lastname ? 'is-invalid' : ''}`}
                      disabled={isLoading}
                      required
                    />
                    {errors.lastname && <div className="invalid-feedback">{errors.lastname}</div>}
                  </div>
                </div>

                {/* Address */}
                <div style={{marginBottom: '20px'}}>
                  <textarea
                    placeholder="🏠 ที่อยู่"
                    value={address}
                    onChange={(e) => setAddress(e.target.value)}
                    className={`form-input ${errors.address ? 'is-invalid' : ''}`}
                    rows="3"
                    disabled={isLoading}
                    required
                    style={{resize: 'vertical'}}
                  />
                  {errors.address && <div className="invalid-feedback">{errors.address}</div>}
                </div>

                {/* Sex and Birth Date */}
                <div className="row">
                  <div className="col-md-6">
                    <div style={{marginBottom: '15px'}}>
                      <p style={{color: '#6f42c1', fontWeight: '500', marginBottom: '10px'}}>🌺 เลือกเพศ</p>
                      <div className="form-check">
                        <input
                          type="radio"
                          name="sex"
                          value="ชาย"
                          checked={sex === 'ชาย'}
                          onChange={(e) => setSex(e.target.value)}
                          disabled={isLoading}
                        />
                        <label>👨 ชาย</label>
                      </div>
                      <div className="form-check">
                        <input
                          type="radio"
                          name="sex"
                          value="หญิง"
                          checked={sex === 'หญิง'}
                          onChange={(e) => setSex(e.target.value)}
                          disabled={isLoading}
                        />
                        <label>👩 หญิง</label>
                      </div>
                      <div className="form-check">
                        <input
                          type="radio"
                          name="sex"
                          value="อื่นๆ"
                          checked={sex === 'อื่นๆ'}
                          onChange={(e) => setSex(e.target.value)}
                          disabled={isLoading}
                        />
                        <label>🌈 อื่นๆ</label>
                      </div>
                      {errors.sex && <div className="invalid-feedback">{errors.sex}</div>}
                    </div>
                  </div>
                  
                  <div className="col-md-6">
                    <input
                      type="date"
                      value={birthday}
                      onChange={(e) => setBirthday(e.target.value)}
                      className={`form-input ${errors.birthday ? 'is-invalid' : ''}`}
                      disabled={isLoading}
                      required
                    />
                    {errors.birthday && <div className="invalid-feedback">{errors.birthday}</div>}
                  </div>
                </div>

                {/* Terms and Conditions */}
                <div style={{marginBottom: '30px'}}>
                  <div className="form-check">
                    <input
                      type="checkbox"
                      checked={acceptTerms}
                      onChange={(e) => setAcceptTerms(e.target.checked)}
                      disabled={isLoading}
                    />
                    <label>
                      🛡️ ฉันยอมรับ <a href="#" className="link-style">เงื่อนไขการใช้งาน</a> และ <a href="#" className="link-style">นโยบายความเป็นส่วนตัว</a>
                    </label>
                  </div>
                  {errors.acceptTerms && <div className="invalid-feedback">{errors.acceptTerms}</div>}
                </div>

                {/* Submit Button */}
                <div style={{textAlign: 'center'}}>
                  <button
                    type="submit"
                    className="submit-btn"
                    disabled={isLoading}
                  >
                    {isLoading ? (
                      <>
                        <span className="loading-spinner"></span>
                        กำลังแก้ไขข้อมูล...
                      </>
                    ) : (
                      <>
                        🌸 ปรับปรุงข้อมูล 🌸
                      </>
                    )}
                  </button>
                </div>
              </form>
            )}

            <div style={{textAlign: 'center', marginTop: '30px'}}>
              <p className="text-muted">
                ต้องการกลับไปหน้าหลัก? 
                <a href="/" className="link-style" style={{marginLeft: '5px'}}>
                  หน้าแรก
                </a> | 
                <a href="/Login" className="link-style" style={{marginLeft: '5px'}}>
                  เข้าสู่ระบบ
                </a>
              </p>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}